/*================ Preview section ================*/
.hero {
  width: 100%;
  height: 810px;
  background-image: url(../img/mountains.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 225px;
  display: flex;
  justify-content: center;
  text-align: center; }

.hero__cover {
  display: flex;
  flex-direction: row;
  flex-direction: column;
  align-items: center; }

.hero__cover__title {
  color: #282828;
  font-family: "Exo Thin";
  font-size: 60px;
  font-weight: 400;
  line-height: 75px;
  letter-spacing: 2.4px; }

.hero__cover__title-2 {
  color: #282828;
  font-family: "Exo Thin";
  font-size: 60px;
  font-weight: 400;
  line-height: 75px;
  letter-spacing: 2.4px;
  margin-bottom: 40px; }

.hero__cover__play {
  margin-bottom: 40px;
  width: 59px;
  -webkit-transition: 1s;
  border-radius: 50%;
  transition: 1s;
  cursor: pointer; }
  .hero__cover__play:hover {
    transform: scale(1.2);
    box-shadow: 0px 0px 36px -9px #323969;
    border-radius: 50%;
    transition: 1s; }

.hero__cover__try {
  color: #ffffff;
  background-color: #ff6d6d;
  font-family: Exo Regular;
  font-size: 16px;
  font-weight: 400;
  /* line-height: 75px; */
  text-transform: uppercase;
  letter-spacing: 3.2px;
  padding: 20px 50px;
  border-radius: 3px;
  margin-bottom: 20px;
  transition: 1s; }
  .hero__cover__try:hover {
    box-shadow: 0px 0px 36px -9px #ff6d6d;
    transition: 1s;
    border-radius: 8px;
    color: #000000; }

.hero__cover__text {
  opacity: 0.6;
  color: #282828;
  font-family: Exo Regular;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.56px; }
